import Web3 from "web3";
import store from "../store/index"
import {
  Toast
} from 'vant';
import {
  i18n
} from "./i18n";
import {
  isProd
} from "./config";
const web3Data = {
  web3: {},
  t: i18n.global.t,
  factoryContract: {},
  web3Provider: {},
  chainIds: [1, 3, 186],
  chainInfo: {
    eth: {
      symbol: 'ETH',
      name: 'ETH',
      decimals: 18,
      chainId: isProd ? 1 : 3,
      chainName: isProd ?
        "Ethereum Mainnet" : "Ropsten Testnet",
      rpcUrls: isProd ? ["https://mainnet.infura.io/v3/"] : ["https://ropsten.infura.io/v3/"],
      blockExplorerUrls: isProd ? ["https://etherscan.io/"] : ["https://ropsten.etherscan.io/"]
    },
    seele: {
      symbol: 'Seele',
      name: 'Seele',
      decimals: 18,
      chainId: 186,
      chainName: "Seele Chain Mainnet",
      rpcUrls: ["https://rpc.seelen.pro"],
      blockExplorerUrls: ["https://seeleview.net/"]
    }
  },
  getBlance: (account) => {
    web3Data.web3.eth.getBalance(account).then((amount) => {
      store.commit(
        "updateCurrentBalance",
        web3Data.web3.utils.fromWei(amount, "ether")
      );
    });
  },
  initWeb3: async () => {
    if (window.ethereum) {
      web3Data.web3Provider = window.ethereum;
      window.ethereum.on("accountsChanged", (accounts) => {
        store.commit("updateAccount", accounts);
        window.location.reload();
      })
      web3Data.web3 = new Web3(web3Data.web3Provider);
      try {
        await window.ethereum.enable();
      } catch (error) {
        Promise.reject(error)
      }
    } else {
      if (window.web3) {
        web3Data.web3Provider = window.web3.currentProvider;
        web3Data.web3 = new Web3(web3Data.web3Provider);
      } else {
        Toast(web3Data.web3.t('web3.暂未检测到钱包插件，请安装MetaMask钱包， 或在imtoken里打开'))
      }
    }
    if (web3Data.web3Provider) {
      web3Data.web3 = new Web3(web3Data.web3Provider);
      web3Data.changeNetwork();
    }
    store.commit("updateProvider", web3Data.web3);
    web3Data.web3.eth.getAccounts().then((accounts) => {
      store.commit("updateAccount", accounts);
      return web3Data.getBlance(accounts[0]);
    });
  },
  changeNetwork: async function () {
    let id = await web3Data.web3.eth.net.getId();
    if (web3Data.chainIds.indexOf(id) !== -1) {
      web3Data.toConnect(id);
    }
  },
  toConnect: function () {
    console.log("toConnect");
    store.commit("updateProvider", web3Data.web3);
    web3Data.web3Provider.on("connect", async info => {
      console.log("connect", info);
    });
    web3Data.web3Provider.on("accountsChanged", (accounts) => {
      let acc = web3Data.web3.utils.toChecksumAddress(accounts[0])
      store.commit("updateAccount", acc)
    });
    web3Data.web3Provider.on("chainChanged", chainId => {
      if (chainId != `0x${web3Data.chainInfo.eth.chainId.toString(16)}` && chainId != `0x${web3Data.chainInfo.seele.chainId.toString(16)}`) {
        store.commit("updateAccount", "");
        console.log(chainId)
        Toast(web3Data.t("web3.請將網路切換到Ethereum主網路。"));
        web3Data.switchChain(web3Data.chainInfo.eth.chainId);
      } else {
        store.commit("updateProvider", web3Data.web3);
        web3Data.web3.eth.getAccounts().then(accounts => {
          let acc = web3Data.web3.utils.toChecksumAddress(accounts[0]);
          store.commit("updateAccount", acc);
          window.location.reload()
        });
      }
    });
    web3Data.web3Provider.on("disconnect", (code, reason) => {
      console.log(code, reason);
    });
  },
  switchChain: async function (chainId) {
    if (window.ethereum) {
      try {
        await web3Data.web3Provider
          .request({
            method: "wallet_switchEthereumChain",
            params: [{
              chainId: `0x${chainId.toString(16)}`
            }]
          })
          .then(() => {
            web3Data.toConnect();
          });
      } catch (error) {
        if (error.code === 4902) {
          await web3Data.addChain(chainId);
        } else {
          Toast(error.message);
        }
      }
    } else {
      Toast(
        web3Data.t("web3.無法在Metamask上設定網路，因為ethereum未定義。")
      );
    }
  },
  addChain: async function (chainId) {
    try {
      let _chainInfo = web3Data.chainInfo
      let _chainName = ''
      for (const key in _chainInfo) {
        if (Object.hasOwnProperty.call(_chainInfo, key)) {
          if (_chainInfo[key].chainId === chainId) {
            _chainName = key
          }
        }
      }
      const data = [{
        chainId: `0x${chainId.toString(16)}`,
        chainName: web3Data.chainInfo[_chainName].chainName,
        nativeCurrency: {
          name: web3Data.chainInfo[_chainName].name,
          symbol: web3Data.chainInfo[_chainName].symbol,
          decimals: web3Data.chainInfo[_chainName].decimals
        },
        rpcUrls: web3Data.chainInfo[_chainName].rpcUrls,
        blockExplorerUrls: web3Data.chainInfo[_chainName].blockExplorerUrls
      }];
      /* eslint-disable */
      await web3Data.web3Provider.request({
        method: "wallet_addEthereumChain",
        params: data
      });
    } catch (error) {
      Toast(web3Data.t(error.message));
    }
  }
}

export default web3Data;
